import React from 'react';
import Footer from './../components/Footer';
import Layout from './../components/layout';
import Container from './../components/Container';
import Row from './../components/Row';
import HelmetHeader from './../components/HelmetHeader';
import ReactPlayer from 'react-player';

const PodcastTag = (props) => (
	<li>
		<a href="/#">{`${props.name}, `}</a>
	</li>
);

class PodcastPost extends React.PureComponent {
	render() {
		const props = this.props;
		const podcastTitle = props.podcastTitle;
		const podcastSrc = props.podcastSrc;
		const tags = props.tags;
		const date = props.podcastDate;
		const description = props.podcastDescription;
		return (
			<div className="blog-post">
				<span className="date-comments">{date}</span>
				<h2>{podcastTitle}</h2>
				<Row>
					<div className="col-md-7">
						<ReactPlayer
							url={podcastSrc}
							id={podcastSrc}
						/>
					</div>
					<div className="col-md-5">
						<div dangerouslySetInnerHTML={{ __html: description }} />
						<ul className="post-tags">
							{tags.map((tag, index) => <PodcastTag key={index} name={tag.name} />)}
						</ul>
					</div>
					{/* <div className="col-md-12">
						<ul className="post-tags">
							{tags.map((tag, index) => <PodcastTag key={index} name={tag.name} />)}
						</ul>
					</div> */}
				</Row>
			</div>
		);
	};
}

